.dashCont{
  display: grid;
  grid-template-columns: 1fr 1fr; /* Two equal-width columns */
  height: 100vh;
  background-color: rgb(0, 0, 0);
}
.dashCont > div > div{ 
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  width: 100%;
}
.miscInfo{
  display: flex;
  margin: 3px;
  height: 98vh;
  flex-direction: column
}
.miscInfo > div{
  margin-top: 7px;
}
.topVehicleCont{
  height: fit-content;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-between;
  margin: 3px;
}
.topVehicleCont > div{
  padding: 13px;
}
.busTypeList{
  border: 1px black solid;
  overflow-y: scroll;
  height: 100%;
}

.BusInfoCont{
  border: 1px black solid;

  display: grid;
  grid-template-columns: 1fr 1fr; /* Two equal-width columns */
  justify-items: center;

  position: relative;
  z-index: 1000;
  right: 0;
  top: 0;
  overflow-y: scroll;
}
.BusInfo{
  border: 1px black solid;
  border-radius: 5px;
  width: 90%;
  position: relative;
  background-color: white;
  margin: 3px;
  padding: 3px;
}
.busMapCont{
  position: relative;
  width: 50vw;
  height: 98vh;
  border-radius: 1%;
  border: 2px black solid;
  overflow: hidden;
  margin: 3px;
}
.grid-child-not-constrained{
  grid-column: span 2;
  padding: 3px;
  position: sticky;
  font-weight: bold;
}


.small_text{
  font-size: 0.5rem;
  display: flex;
  justify-content: flex-end;
}

.leaflet-container {
  height: 100vh;
  width: 100wh;
}

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
  background: #ffffff; 
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #000985; 
}
::-webkit-scrollbar-thumb:hover {
  background: #444;
}

